function phone (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: ''
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function login (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: ''
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: ''
    }
  } else if (data.password === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '6'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function signup (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: ''
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: ''
    }
  } else if (data.password === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '6'
    }
  } else if (data.code === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.code.length !== 4) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function forget (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: ''
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: ''
    }
  } else if (data.code === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.code.length !== 4) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function setPassword (data) {
  if (data.password === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '6'
    }
  } else if (data.password !== data.password_confirmation) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function editPassword (data) {
  if (data.old_password === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.old_password.length < 6) {
    return {
      status: false,
      text: '6'
    }
  } else if (data.password === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '6'
    }
  } else if (data.old_password === data.password) {
    return {
      status: false,
      text: ''
    }
  } else if (data.password !== data.password_confirmation) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function upload (data) {
  if (data.name === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.describe === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.describe.length > 200) {
    return {
      status: false,
      text: ''
    }
  } else if (data.images.length === 0) {
    return {
      status: false,
      text: ''
    }
  } else if (data.category_id === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.visible === '') {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function expert (data) {
  let regIdnumber = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/

  if (data.realname === '') {
    return {
      status: false,
      text: ''
    }
  } if (data.province === '' || data.city === '' || data.district === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.category_ids === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.title === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.summary === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.price === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.mobile === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.email === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.idnumber === '') {
    return {
      status: false,
      text: ''
    }
  } else if (!regIdnumber.test(data.idnumber)) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 
function username (name) {
  let reg = /[\u4e00-\u9fa5]/
  let len = 0

  if (reg.test(name)) {
    len = name.length * 2
  } else {
    len = name.length
  }

  if (name === '') {
    return {
      status: false,
      text: ''
    }
  } else if (len > 10) {
    return {
      status: false,
      text: '10'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 
function price (price) {
  if (price === '') {
    return {
      status: false,
      text: ''
    }
  } else if (isNaN(price)) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 
function ticket (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.content === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.mobile === '') {
    return {
      status: false,
      text: ''
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 
function withdraws (data) {
  if (data.name === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.bank === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.bank_account === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.money === '') {
    return {
      status: false,
      text: ''
    }
  } else if (isNaN(data.money)) {
    return {
      status: false,
      text: ''
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function evaluations (data) {
  if (data.academic_name === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.years === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.evaluate === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.historical_value === '') {
    return {
      status: false,
      text: ''
    }
  } else if (data.historical_value === '') {
    return {
      status: false,
      text: ''

    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

export default {
  phone,
  login,
  signup,
  forget,
  setPassword,
  editPassword,
  upload,
  expert,
  username,
  price,
  ticket,
  withdraws,
  evaluations
}



